<template>
  <v-row justify="center">
    <v-col cols="12" md="10" lg="8">
      <v-card>
        <v-card-title>
          Apply Project
        </v-card-title>

        <v-card-text>
          <v-form ref="form" v-model="form">
            <v-row>
              <v-col cols="12" v-if="data.questions" sm="8">
                <template v-for="(q, index) in answer">
                  <v-textarea
                    :label="`${q.question}`"
                    :key="`question-${index}`"
                    :prefix="`*`"
                    :rules="[rules.requiredAnswer]"
                    v-model="q.content"
                    rows="1"
                  >
                  </v-textarea>
                </template>
              </v-col>
              <!-- -->
              <template v-if="data.type !== 'academic-team'">
                <v-col cols="12" v-if="data.cost_max > 0" sm="8">
                  <v-text-field
                    v-model="cost"
                    :label="
                      data.type === 'academic-team' && data.provide_solution
                        ? 'Total Wage for Academic team'
                        : 'Cost'
                    "
                    :rules="[
                      rules.required,
                      rules.cost(data.cost_min, data.cost_max),
                    ]"
                    :hint="`Must be between $${data.cost_min} - $${data.cost_max}`"
                  >
                    <v-icon slot="prepend" color="red">mdi-currency-usd</v-icon>
                  </v-text-field>
                </v-col>
              </template>
              <!--  -->
              <template v-if="data.type === 'academic-team'">
                <!--    -->
                <!-- 1.  -->
                <template
                  v-if="data.provide_solution && !data.is_government_subsidy"
                >
                  <v-col cols="12" sm="8">
                    <v-text-field
                      v-model="project_budget"
                      label="Actual project cost"
                      :rules="[rules.required]"
                    >
                      <v-icon slot="prepend" color="red"
                        >mdi-currency-usd</v-icon
                      >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" sm="8">
                    <v-text-field
                      v-model="cost"
                      :label="'Wage for academic team'"
                      :rules="[rules.required]"
                    >
                      <v-icon slot="prepend" color="red"
                        >mdi-currency-usd</v-icon
                      >
                    </v-text-field>
                  </v-col>
                </template>
                <!-- 2.   -->
                <template
                  v-if="data.provide_solution && data.is_government_subsidy"
                >
                  <v-col cols="12" sm="8">
                    <v-text-field
                      v-model="project_budget"
                      label="Actual Project Cost"
                      :rules="[rules.required]"
                    >
                      <v-icon slot="prepend" color="red"
                        >mdi-currency-usd</v-icon
                      >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" sm="8">
                    <v-text-field
                      disabled
                      :value="data.students_budget"
                      label="Wage for hiring students"
                    >
                      <v-icon slot="prepend" color="red"
                        >mdi-currency-usd</v-icon
                      >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" sm="8">
                    <v-text-field
                      v-model="cost"
                      :rules="[rules.required]"
                      label="Wage for hiring team lead"
                    >
                      <v-icon slot="prepend" color="red"
                        >mdi-currency-usd</v-icon
                      >
                    </v-text-field>
                  </v-col>
                </template>
                <!-- 3.  -->
                <template
                  v-if="!data.provide_solution && data.is_government_subsidy"
                >
                  <v-col cols="12" sm="8">
                    <v-text-field
                      disabled
                      :value="data.students_budget"
                      label="Wage for hiring students"
                    >
                      <v-icon slot="prepend" color="red"
                        >mdi-currency-usd</v-icon
                      >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" sm="8">
                    <v-text-field
                      disabled
                      :value="data.team_lead_budget"
                      label="Wage for hiring team lead"
                    >
                      <v-icon slot="prepend" color="red"
                        >mdi-currency-usd</v-icon
                      >
                    </v-text-field>
                  </v-col>
                </template>
                <!-- 4.  -->
                <template
                  v-if="!data.provide_solution && !data.is_government_subsidy"
                >
                  <v-col cols="12" sm="8">
                    <v-text-field
                      disabled
                      :value="data.cost_min"
                      label="Wage for academic team"
                    >
                      <v-icon slot="prepend" color="red"
                        >mdi-currency-usd</v-icon
                      >
                    </v-text-field>
                  </v-col>
                </template>
              </template>
              <v-col
                cols="12"
                sm="8"
                v-if="data.type === 'academic-team' && data.provide_solution"
              >
                <v-sheet class="upload-btn">
                  <v-text-field
                    v-model="file_url"
                    prepend-inner-icon="mdi-paperclip"
                    label="Upload your solution proposal"
                    :disabled="true"
                  ></v-text-field>
                  <v-btn @click="fileUpload" :loading="uploading" elevation="1">
                    <v-icon>mdi-upload</v-icon>Upload
                  </v-btn>
                  <v-btn
                    text
                    class="tw-ml-4"
                    color="primary"
                    href="https://docs.google.com/document/d/1MTSp1O8LYxAptZ1WB9dtiv8tblGx98CdUJJzt3XTN2M/edit"
                    target="_blank"
                    >Download the solution proposal template</v-btn
                  >
                  <input
                    ref="file"
                    type="file"
                    class="file-input"
                    accept="image/*, .pdf, .doc"
                    @change="handleUpload"
                  />
                </v-sheet>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn
            :disabled="!form || isLoading || uploading"
            :loading="isLoading"
            @click="apply"
            >Apply</v-btn
          >
          <v-spacer></v-spacer>
          <v-btn @click="$router.go(-1)">Back</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>
<script>
import { apply as applyProject, fetchData } from '@/api/project'
import { get } from 'lodash'
import axios from '@/plugins/axios'
const uploadUrl = process.env.VUE_APP_BASE_URL + 'upload'
export default {
  computed: {
    id() {
      return this.$route.params.id || 0
    },
  },
  watch: {
    'data.questions'(questions) {
      if (Array.isArray(questions)) {
        questions.forEach((item, index) => {
          this.$set(this.answer, index, {
            question: item.title,
            required: item.checked,
            content: '',
          })
        })
      }
    },
  },
  data: () => ({
    form: false,
    isLoading: false,
    cost: null,
    answer: [],
    file: null,
    file_url: null,
    rules: {
      required: (v) => !!v || 'This field is required',
      requiredAnswer: (v) => !!v || 'Please answer the questions',
      cost: (min, max) => (value) =>
        (value >= parseFloat(min) && value <= parseFloat(max)) ||
        `Must be $${min} - $${max}`,
    },
    data: {
      questions: [],
      id: 0,
      students_budget: null,
    },
    uploading: false,
    team_lead_budget: null,
    total_budget: null,
    project_budget: null,
  }),
  async mounted() {
    const data = this.$store.state.project.data

    if (get(data, 'id', -1) === this.id) {
      this.data = this.$store.state.project.data
    } else {
      await this.loadData()
    }
  },
  methods: {
    async loadData() {
      const { data } = await fetchData(this.id)
      this.data = data
    },
    async apply() {
      this.isLoading = true
      try {
        const applyData = {
          cost: parseInt(this.cost),
          answer: this.answer,
          password: this.$store.state.project.password,
          project_budget: parseInt(this.project_budget),
          students_budget: this.data.students_budget,
        }
        console.log(this.data)
        if (
          this.file_url &&
          this.data.type === 'academic-team' &&
          this.data.provide_solution
        ) {
          applyData.file = this.file_url
        }
        const res = await applyProject(this.data.id, applyData)
        if (res.message) {
          this.$store.dispatch('message/pushMessage', {
            type: 'info',
            content: res.message,
            timeout: 3000,
          })
          this.$router.push(`/projects/${this.id}`)
        }
        this.isLoading = false
      } catch (e) {
        this.isLoading = false
      }
    },
    fileUpload() {
      this.$refs['file'].click()
    },
    // 
    async handleUpload(e) {
      const file = e.target.files[0]
      this.uploading = true
      try {
        const data = new FormData()
        data.append('upload_file', file)
        const res = await axios.post('upload', data)
        this.file_url = res.file_path
        this.uploading = false
      } catch (e) {
        console.log(e)
        this.uploading = false
      }
    },
  },
}
</script>
<style lang="scss" scoped>
.file-input {
  width: 0;
  height: 0;
}
</style>
